<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <div slot="left" color="#3051f1">
        <a @click="()=>{
            $router.go(-1)
          }">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">处理结果</span>
      </div>
    </mu-appbar>
    <div class="backHandle">
      <div>
        <div class="backWhite">
          <div>
            <span>处理结果及描述</span>
          </div>
          <textarea name id="textarea" cols="30" disabled rows="10"></textarea>
        </div>
        <div class="backWhite">
          <div>
            <span>现场照片</span>
          </div>
          <mu-container>
            <mu-expansion-panel
              :expand="panel === 'panel1'"
              @change="toggle('panel1')"
              class="paddingHandle"
              v-for="(item,index) in itemListPic"
              :key="index"
            >
              <div slot="header">图片{{index+1}}</div>
              <div class="imageSize" v-for="(itemChild ,index1) in item.livePhoto" :key="index1">
                <img :src="url+itemChild" alt />
              </div>
            </mu-expansion-panel>
          </mu-container>
        </div>

        <div class="handlePerson backWhite">
          <div>
            <span>处理人</span>
          </div>
          <input id="input" type="text" disabled />
        </div>
      </div>
      <!-- <div>
        <div class="submit">
          <span>提交</span>
        </div>
      </div>-->
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Warning from "../../../static/js/warning.js";
export default {
  name: "handle",
  data() {
    return {
      panel: "",
      itemListPic: [],
      token: "",
      id: "",
      url: "http://47.112.16.168:"
    };
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData(id, st, lt, ws, ps, pn, wt) {
      var url = "/warning/list";
      var waterStation = ws;
      var starttime = st;
      var lasttime = lt;
      var pageSize = ps;
      var pageNum = pn;
      var warningType = wt;
      var res = await AppBace.request(
        url,
        "POST",
        {
          waterStation,
          starttime,
          lasttime,
          pageSize,
          pageNum,
          warningType
        },
        this.token
      );
      // console.log(res.data.data.data);
      this.itemListPic.push(Warning.filterData(res.data.data.data, this.id));
      //  console.log(this.itemListPic);
      //  debugger
      $("#input").val(this.itemListPic[0].processingPerson);
      $("#textarea").val(this.itemListPic[0].processingMark);

      // this.listWarningContent = res.data.data.data;
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.id = this.$route.query.id;
    var starttime = this.$route.query.startTime;
    var lasttime = this.$route.query.lastTime;
    var waterStation = this.$route.query.waterStation;
    var pageSize = 10;

    var index = this.$route.query.index;
    var pageNum = Math.ceil(index / pageSize);
    var warningType = this.$route.query.warningType;

    this.initData(
      this.id,
      starttime,
      lasttime,
      waterStation,
      pageSize,
      pageNum,
      warningType
    );
  }
};
</script>
<style scoped>
#textarea {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  border: #ccc 1px solid;
  outline: none;
  padding: 5px;
  background: rgb(249, 254, 255);
}
.backHandle {
  color: rgb(122, 122, 122);
  padding: 10px;
  margin-top: 60px;
}
.backWhite {
  background: white;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 5px;
}
.handlePerson {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: space-between;
}
#input {
  border-radius: 5px;
  border: rgb(231, 231, 231) 2px solid;
  border: none;
  margin-left: 10px;
  outline: none;
  padding: 0 5px;
  text-align: right;
  background: white;
}
.imageSize img {
  width: 100%;
  height: 200px;
}
.submit {
  width: 100%;
  text-align: center;
  height: 40px;
  background: #4ed995;
  border-radius: 10px;
  color: white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.header {
  position: fixed;
  top: 0;
}
.paddingHandle {
  box-shadow: none;
}
</style>



